.App {
  display: flex;
  flex-direction: column;
  margin: 10vh auto;
  gap: 5vh;
  width: 80vw;
}

h1 {
  text-align: center;
}

button {
  padding: 10px;
  align-self: center;
  border-radius: 10px;
  border: 3px solid rgb(29, 28, 28);
  cursor: pointer;
}

img {
  width: 40vw;
  margin-top: 10vh;
}

p {
  background: rgb(9, 183, 241);
  color: white;
  padding: 20px;
  border-radius: 10px;
}

.agree-btn {
  background: rgb(236, 139, 139);
}

.disagree-btn {
  background: lightgreen;
}
